import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: '/',
    redirect: '/ludo'
  },
  {
    path: "/login",
    name: "login",
    component: () => import(/* webpackChunkName: "auth" */ "../views/auth/login.vue")
  },
  {
    path: "/chess/:id?",
    name: "chess",
    component: () => import(/* webpackChunkName: "chess" */ "../views/chess/chess.vue")
  },
  {
    path: "/hanoi/:id?",
    name: "hanoi",
    component: () => import(/* webpackChunkName: "hanoi" */ "../views/hanoi/hanoi.vue")
  },
  {
    path: "/ludo/:id?",
    name: "ludo",
    component: () => import(/* webpackChunkName: "ludo" */ "../views/ludo/ludo.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
