import firebase from 'firebase';

const database = firebase.database();

export default {
    writeUserData(userId: string, user: Object) {
        database.ref('users/' + userId).set(user);
    },
    get() {
        return database.ref('users').once('value').then(function (snapshot) {
            return snapshot.val()
        });
    },
    getById(userId: string) {
        return database.ref('users/' + userId).once('value').then(function (snapshot) {
            return snapshot.val()
        });
    },
    updateById(userId: string, user: Object) {
        return database.ref('users/' + userId).update(user).then((response) => {
            console.log(response);

        });
    },
    onValueChange(callback: (arg0: any) => void) {
        database.ref('users').on('value', function (snapshot) {
            callback(snapshot.val());
        });
    }
}